/**
 * 格式化日份（2023-01-29）
 */
export function formatDate (date) {
  var year = date.getFullYear().toString();
  var month = (date.getMonth() + 1).toString().padStart(2, "0");
  var date = date
    .getDate()
    .toString()
    .padStart(2, "0");
  var result = year + "-" + month + "-" + date;
  return result;
}