<template>
  <div class="partner-company">
    <el-container>
      <el-header class="header">
        <el-form
          :inline="true"
          :model="searchValidateForm"
          ref="searchValidateForm"
          label-width="100px"
          class="search-validate-form"
          :rules="searchValidateFormRules"
        >
          <!-- 单选景区多选时间范围 start -->
          <template v-if="searchValidateForm.mode == 0">
            <!-- 输入起止时间 start -->
            <el-form-item label="" prop="startEndDate">
              <el-date-picker
                v-model="searchValidateForm.startEndDate"
                unlink-panels
                :type="dateType"
                range-separator="至"
                start-placeholder="开始日期"
                end-placeholder="结束日期"
              >
              </el-date-picker>
            </el-form-item>
            <!-- 输入起止时间 end -->

            <el-form-item label="" prop="mode">
              <!-- 选择模式 start -->
              <el-select
                v-model="searchValidateForm.mode"
                filterable
                placeholder="请选择模式"
                clearable
                @change="onChangeMode"
              >
                <el-option
                  v-for="(item, index) in searchModeOptions"
                  :key="index"
                  :label="item.label"
                  :value="item.value"
                >
                </el-option>
              </el-select>
              <!-- 选择模式 end -->
            </el-form-item>

            <el-form-item label="" prop="timeSpotId">
              <!-- 选择景区名称 start -->
              <el-select
                v-model="searchValidateForm.timeSpotId"
                filterable
                placeholder="请选择景区"
                clearable
              >
                <el-option
                  v-for="(item, index) in searchScenicOptions"
                  :key="index"
                  :label="item.SCENIC_SPOT_NAME"
                  :value="item.SCENIC_SPOT_ID"
                >
                </el-option>
              </el-select>
              <!-- 选择景区名称 end -->
            </el-form-item>
          </template>
          <!-- 单选景区多选时间范围 end -->

          <!-- 单选日期多选景区 start -->
          <template v-if="searchValidateForm.mode == 1">
            <el-form-item label="" prop="spotIdList">
              <!-- 选择景区名称 start -->
              <el-select
                v-model="searchValidateForm.spotIdList"
                filterable
                placeholder="请选择景区"
                clearable
                multiple
                collapse-tags
                v-if="updateSelectSpotIdList"
              >
                <el-option
                  v-for="(item, index) in searchScenicOptions"
                  :key="index"
                  :label="item.SCENIC_SPOT_NAME"
                  :value="item.SCENIC_SPOT_ID"
                >
                </el-option>
              </el-select>
              <!-- 选择景区名称 end -->
            </el-form-item>

            <el-form-item label="" prop="mode">
              <!-- 选择模式 start -->
              <el-select
                v-model="searchValidateForm.mode"
                filterable
                placeholder="请选择模式"
                clearable
                @change="onChangeMode"
              >
                <el-option
                  v-for="(item, index) in searchModeOptions"
                  :key="index"
                  :label="item.label"
                  :value="item.value"
                >
                </el-option>
              </el-select>
              <!-- 选择模式 end -->
            </el-form-item>

            <!-- 输入时间 start -->
            <el-form-item label="" prop="date">
              <el-date-picker
                v-model="searchValidateForm.date"
                type="date"
                placeholder="请选择日期"
                v-if="updateSelectDate"
              />
            </el-form-item>
            <!-- 输入时间 end -->
          </template>
          <!-- 单选日期多选景区 end -->

          <!-- 单选景区多选日期 start -->
          <template v-if="searchValidateForm.mode == 2">
            <!-- 输入起止时间 start -->
            <el-form-item label="" prop="multipeDate">
              <div @click.stop>
                <el-date-picker
                  id="mydates"
                  ref="mydates"
                  v-model="searchValidateForm.multipeDate"
                  unlink-panels
                  type="dates"
                  :editable="false"
                  range-separator="至"
                  placeholder="请选择日期"
                  :default-value="new Date()"
                  @focus="onFocusDate"
                >
                </el-date-picker>
              </div>
            </el-form-item>
            <!-- 输入起止时间 end -->

            <el-form-item label="" prop="mode">
              <!-- 选择模式 start -->
              <el-select
                v-model="searchValidateForm.mode"
                filterable
                placeholder="请选择模式"
                clearable
                @change="onChangeMode"
              >
                <el-option
                  v-for="(item, index) in searchModeOptions"
                  :key="index"
                  :label="item.label"
                  :value="item.value"
                >
                </el-option>
              </el-select>
              <!-- 选择模式 end -->
            </el-form-item>

            <el-form-item label="" prop="spotId">
              <!-- 选择景区名称 start -->
              <el-select
                v-model="searchValidateForm.spotId"
                filterable
                placeholder="请选择景区"
                clearable
                v-if="updateSelectSpotId"
              >
                <el-option
                  v-for="(item, index) in searchScenicOptions"
                  :key="index"
                  :label="item.SCENIC_SPOT_NAME"
                  :value="item.SCENIC_SPOT_ID"
                >
                </el-option>
              </el-select>
              <!-- 选择景区名称 end -->
            </el-form-item>
          </template>
          <!-- 单选景区多选日期 end -->
        </el-form>

        <!-- 搜索 start -->
        <el-button
          type="primary"
          icon="el-icon-search"
          @click="onSearch('searchValidateForm')"
          >搜索</el-button
        >
        <!-- 搜索 end -->

        <!-- 刷新 start -->
        <el-button
          type="primary"
          icon="el-icon-refresh"
          @click="onRefresh('searchValidateForm')"
          >刷新</el-button
        >
        <!-- 刷新 end -->

        <!-- 导出 Excel start -->
        <!-- <el-button type="primary" :loading="downloadLoading" icon="el-icon-top-right" @click="exportExcel">导出Excel</el-button> -->

        <!-- 导出 Excel end -->

        <!-- 新增 start -->
        <!-- <el-button type="primary" icon="el-icon-circle-plus-outline" @click="onAdd">新增</el-button> -->
        <!-- 新增 end -->
      </el-header>
      <el-main>
        <!-- 显示柱状图 start -->
        <div class="my-echarts">
          <div id="myChart"></div>
        </div>
        <!-- 显示柱状图 end -->
      </el-main>
      <el-footer> </el-footer>
    </el-container>
  </div>
</template>

<script>
// import JsonExcel from 'vue-json-excel'
import FileSaver from 'file-saver'
import XLSX from 'xlsx'
import { formatDate } from '../utils/utils.js'
export default {
  name: 'PartnerCompany',
  data() {
    /**
     * 公共验证函数(验证数字值)
     * @params message - 提示信息
     */
    function checkNumberMethod(name = '') {
      // 返回验证函数
      return (rule, value, callback) => {
        if (!value) {
          // 如果值为空，可以回调
          callback()
        }

        if (isNaN(Number(value))) {
          callback(new Error(name + '必须为数字值'))
        } else {
          callback()
        }
      }
    }

    /**
     * 公共验证函数(验证必填数字值)
     * @params message - 提示信息
     */
    function checkRequireNumberMethod(name = '') {
      // 返回验证函数
      return (rule, value, callback) => {
        if (!value) {
          return callback(new Error(name + '不能为空'))
        }

        if (isNaN(Number(value))) {
          callback(new Error(name + '必须为数字值'))
        } else {
          callback()
        }
      }
    }

    /**
     * 公共验证函数(验证必填值)
     * @params message - 提示信息
     */
    function checkRequireMethod(name = '') {
      // 返回验证函数
      return (rule, value, callback) => {
        if (!value) {
          return callback(new Error(name + '不能为空'))
        } else {
          callback()
        }
      }
    }

    return {
      // input: '', // 请输入公司名称
      searchCompanyName: '', // 搜索的公司名称
      tableFields: {
        //导出Excel表格的表头设置
        合作公司名称: 'companyName',
        收款账户: 'collectionAccount',
        收款账号: 'collectionAccountNumber',
        开户行: 'bank',
        创建时间: 'createTime',
        备注: 'notes'
      },
      tableData: [], // 表格数据
      exportTableData: [], // 导出的全部表格数据
      downloadLoading: false, // 导出excel按钮的加载动画

      loadingTable: false,

      currentPage: 1, // 当前页数,
      pageSize: 10, // 每页显示条数

      dialogVisible: false, // 是否显示dialog对话框
      dialogState: '', // dialog是新增还是编辑表单，新增为'add'，编辑为'edit'

      dateType: 'daterange', // 日期类型（月，日，年）
      valueFormat: 'yyyy-MM-dd', // 日期格式

      updateSelectSpotIdList: true, // 是否显示单选日期多选景区中的景区选项框
      updateSelectDate: true, // 是否显示单选日期多选景区中的日期选项框
      updateSelectSpotId: true, // 是否显示单选景区多选时间范围中的景区选项框

      searchModeOptions: [
        {
          label: '单选景区多选时间范围',
          value: 0
        },
        {
          label: '单选日期多选景区',
          value: 1
        },
        {
          label: '单选景区多选日期',
          value: 2
        }
      ],

      searchScenicOptions: [],

      searchValidateForm: {
        mode: 0, // 0单选景区多选时间范围 1单选日期多选景区 2单选景区多选日期
        spotIdList: [],
        spotId: '',
        timeSpotId: '',
        date: new Date(),
        startEndDate: [new Date(), new Date()],
        multipeDate: []
      }, // 搜索表单验证对象
      searchVF: {
        mode: 2,
        spotIdList: [],
        spotId: '',
        timeSpotId: '',
        date: '',
        startEndDate: [],
        multipeDate: []
      }, // 搜索需要提交的表单
      validateForm: {},
      total: 0,

      myChart: null, // echarts实例
      option: {}, // echarts option
      realTimeList: [], // 时间列表
      drysList: [], // 当日营收列表
      jxzList: [], // 当日在线订单列表

      // 提交表单验证规则
      validateFormRules: {
        // 合作公司名称
        companyName: [
          {
            validator: checkRequireMethod('合作公司名称'),
            required: true,
            trigger: ['blur', 'change']
          }
        ],

        // 收款账户
        collectionAccount: [
          {
            validator: checkRequireMethod('收款账户'),
            required: true,
            trigger: ['blur', 'change']
          }
        ],

        // 收款账号
        collectionAccountNumber: [
          {
            validator: checkRequireMethod('收款账号'),
            required: true,
            trigger: ['blur', 'change']
          }
        ],

        // 开户行
        bank: [
          {
            validator: checkRequireMethod('开户行'),
            required: true,
            trigger: ['blur', 'change']
          }
        ]
      },

      // 搜索表单验证规则
      searchValidateFormRules: {},

      timer: null
    }
  },
  created() {
    // 接收缓存中的 realTimeLineSpotIdCopy
    this.searchValidateForm.timeSpotId = JSON.parse(
      window.sessionStorage.getItem('realTimeLineSpotIdCopy')
    )

    // 如果当前环境是开发环境才调用登录方法
    if (process.env.NODE_ENV == 'development') {
      this.login()
    }

    // this.initTableData()
    this.getValidateForm()
    this.getAllSpotList()
  },
  mounted() {
    // 监听当前页面是不是从柱点过来的
    const listenSpot = () => {
      this.timer = setInterval(() => {
        console.log('监听')
        var realTimeLineSpotId = JSON.parse(
          window.sessionStorage.getItem('realTimeLineSpotId')
        )

        if (realTimeLineSpotId) {
          // 设置一个新的缓存
          window.sessionStorage.setItem(
            'realTimeLineSpotIdCopy',
            realTimeLineSpotId
          )
          // 删除旧缓存
          window.sessionStorage.removeItem('realTimeLineSpotId')
          window.location.reload()
        }
      }, 1000)
    }
    listenSpot()

    // 监听页面打开状态
    document.addEventListener('visibilitychange', () => {
      const state = document.visibilityState
      // console.log(state) // hidden 表示当前页面切换为隐藏状态，visible表示当前页面切换为浏览状态
      if (state == 'visible') {
        listenSpot()
      } else {
        clearInterval(this.timer)
      }
    })
    // this.drawEcharts()
    this.getRevenueLine()
  },
  beforeDestroy() {
    clearInterval(this.timer)
  },
  methods: {
    // 获取表单验证对象
    getValidateForm() {
      var validateForm = {}
      for (var item in this.tableFields) {
        validateForm[this.tableFields[item]] = ''
      }
      this.validateForm = validateForm
      // console.log(this.validateForm);
    },

    // 登录(必须)
    async login() {
      const { data: res } = await this.$http.post(
        '/loginSystem?username=lijiazhao&password=lijiazhao'
      )
    },

    /**
     * 初始化表格数据
     */
    async initTableData() {
      var companyName = this.searchVF.companyName
      if (companyName == '' || !companyName) {
        companyName = null
      }

      this.loadingTable = true
      const { data: res } = await this.$http.get(
        '/system/cooperative_company/list',
        {
          params: {
            name: companyName, // 公司名称(模糊查询)
            // this.currentPage
            pageNum: (this.currentPage - 1) * this.pageSize, // 页码
            pageSize: this.pageSize // 每页条数
          }
        }
      )
      // console.log(res);
      if (res.state == '200') {
        this.tableData = res.data
        this.total = res.total
        this.$message({
          message: res.msg,
          type: 'success',
          offset: 100,
          center: true,
          duration: 1000
        })
        this.loadingTable = false
      } else {
        this.$message({
          message: res.msg,
          type: 'error',
          offset: 100,
          center: true,
          duration: 3000
        })
        return
      }
    },

    /**
     * 新增表格数据
     */
    async addTableData() {
      const { data: res } = await this.$http.post(
        '/system/cooperative_company/add',
        this.validateForm
      )
      if (res.state == '200') {
        this.$message({
          message: res.msg,
          type: 'success',
          offset: 100,
          center: true,
          duration: 1000
        })
        this.initTableData()
        this.$refs.validateForm.resetFields() // 重置表单
      } else {
        this.$message({
          message: res.msg,
          type: 'error',
          offset: 100,
          center: true,
          duration: 3000
        })
      }
    },

    /**
     * 修改表格数据
     */
    async editTableData() {
      const { data: res } = await this.$http.post(
        '/system/cooperative_company/edit',
        this.validateForm
      )
      if (res.state == '200') {
        this.$message({
          message: res.msg,
          type: 'success',
          offset: 100,
          center: true,
          duration: 1000
        })
        this.initTableData()
      } else {
        this.$message({
          message: res.msg,
          type: 'error',
          offset: 100,
          center: true,
          duration: 3000
        })
      }
    },

    /**
     * 获取导出表格的全部数据
     */
    async getExportTableData() {
      const { data: res } = await this.$http.get(
        '/system/cooperative_company/list'
      )
      // console.log(res);
      if (res.state == '200') {
        this.exportTableData = res.data
      } else {
        return
      }
    },

    /**
     * 获取景区列表
     */
    async getAllSpotList() {
      const { data: res } = await this.$http.get(
        '/system/robot_time_statistics/spotList'
      )
      this.searchScenicOptions = res.data
    },

    /**
     * 获取当日营收曲线数据
     */
    async getRevenueLine() {
      this.drawEcharts()

      var searchSpotIdList = this.searchValidateForm.spotIdList
      var searchSpotId = this.searchValidateForm.spotId
      var searchTimeSpotId = this.searchValidateForm.timeSpotId
      var spotId = ''

      var date = this.searchValidateForm.date
      var startEndDate = this.searchValidateForm.startEndDate
      var multipeDate = this.searchValidateForm.multipeDate
      var mode = this.searchValidateForm.mode
      var time, startDate, endDate, mDate

      if (mode == 0) {
        // 单选景区多选时间范围
        if (searchTimeSpotId == '' || !searchTimeSpotId) {
          spotId = null
        } else {
          spotId = searchTimeSpotId
        }

        if (startEndDate.length == 0 || !startEndDate) {
          startDate = null
          endDate = null
        } else {
          startDate = formatDate(startEndDate[0])
          endDate = formatDate(startEndDate[1])
        }
      } else if (mode == 1) {
        // 单选日期多选景区
        if (searchSpotIdList.length == 0 || !searchSpotIdList) {
          spotId = null
        } else {
          spotId = searchSpotIdList.join(',')
        }

        if (date == '' || !date) {
          time = null
        } else {
          time = formatDate(date)
        }
      } else if (mode == 2) {
        // 单选景区多选日期
        if (searchSpotId == '' || !searchSpotId) {
          spotId = null
        } else {
          spotId = searchSpotId
        }

        if (startEndDate.length == 0 || !startEndDate) {
          mDate = null
        } else {
          mDate = multipeDate
            .reduce((res, item) => {
              return res + ',' + formatDate(item)
            }, '')
            .slice(1)
        }
      }

      // 加载Echarts loading效果
      this.myChart.showLoading({
        text: 'loading',
        color: '#c23531',
        textColor: '#000',
        maskColor: 'rgba(255, 255, 255, 0.2)',
        zlevel: 0
      })

      const { data: res } = await this.$http.get('/system/realTime/lineN', {
        params: {
          spotId,
          time,
          startTime: startDate,
          endTime: endDate,
          multipeDate: mDate,
          type: mode
        }
      })

      if (res.state == '200') {
        // 隐藏echart的loadnig效果
        this.myChart.hideLoading()

        // var realTimeList = []
        // var drysList = []
        // var jxzList = []

        var maxLy = res.data.drysMax // 最大当日营收金额
        var maxRy = res.data.jxzMax // 最大订单条数
        var gap = 6 // 分隔段数
        var intervalLy = null // 当日营收金额分隔间隔数
        var intervalRy = null // 订单条数分隔间隔数

        // res.data.forEach((item) => {
        //   realTimeList.push(item.REAL_TIME)
        //   drysList.push(item.drys)
        //   jxzList.push(item.jxz)
        //   if (maxLy < item.drys) {
        //     maxLy = item.drys
        //   }
        //   if (maxRy < item.jxz) {
        //     maxRy = item.jxz
        //   }
        // })

        // console.log(maxLy);
        // console.log(maxRy);
        // 如果大于6条则设置为null，否则设置为6
        if (maxLy <= gap) {
          maxLy = gap
        }

        if (maxRy <= gap) {
          maxRy = gap
        }

        // 计算间隔数 = 当前最大值 / 分隔段数，再向上取整
        intervalLy = Math.ceil(maxLy / gap)
        intervalRy = Math.ceil(maxRy / gap)

        // 计算左侧为间隔倍数的最大值
        maxLy = intervalLy * gap
        // 计算右侧为间隔倍数的最大值
        maxRy = intervalRy * gap

        this.option.legend.data = res.data.nameList
        this.option.xAxis[0].data = res.data.dateList
        this.option.yAxis[0].interval = intervalLy
        this.option.yAxis[1].interval = intervalRy
        this.option.yAxis[0].max = maxLy
        this.option.yAxis[1].max = maxRy

        // this.option.series[0].data = drysList
        // this.option.series[1].data = jxzList
        this.option.series = res.data.lineList

        this.myChart.setOption(this.option)
      } else {
        this.$message({
          type: 'error',
          message: '无数据',
          offset: 100,
          center: true,
          duration: 3000
        })
        // 隐藏echart的loadnig效果
        this.myChart.hideLoading()
      }
    },

    /**
     * 查询公司名称获取列表数据
     */
    getTableData() {
      // console.log(this.searchValidateForm.companyName, this.searchInput);
      // 如果输入框中的内容等于上次搜索的内容，则直接返回
      if (this.searchValidateForm.spotId == this.searchVF.spotId) {
        return
      }

      // 保存当前搜索的内容
      this.searchVF.spotId = this.searchValidateForm.spotId

      // 否则调用接口，请求数据，为tableData重新赋值
      console.log('调用接口')
      this.initTableData()
    },
    // 仅在输入框失去焦点或用户按下回车时触发
    onChange() {
      // this.getTableData()
    },
    // 点击搜索按钮时触发
    onSearch(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.getRevenueLine()
          // this.getTableData()
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },

    // 点击刷新按钮时触发
    onRefresh() {
      this.searchValidateForm.spotIdList = []
      this.searchValidateForm.spotId = ''
      this.searchValidateForm.mode = 0
      this.searchValidateForm.date = new Date()
      this.searchValidateForm.startEndDate = [new Date(), new Date()]
      this.searchValidateForm.multipeDate = [new Date()]

      // 更新单选景区多选日期的景区下拉选
      this.updateSelectSpotIdList = false
      this.$nextTick(() => {
        this.updateSelectSpotIdList = true
      })

      // 更新单选景区多选日期的日期下拉选
      this.updateSelectDate = false
      this.$nextTick(() => {
        this.updateSelectDate = true
      })

      // 更新单选景区多选时间范围的景区下拉选
      this.updateSelectSpotId = false
      this.$nextTick(() => {
        this.updateSelectSpotId = true
      })

      this.getRevenueLine()
    },

    // 导出Excel表格
    exportExcel() {
      this.$confirm('确认导出Excel表格?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      })
        .then(async () => {
          this.downloadLoading = true

          // 请求全部数据
          await this.getExportTableData() // 这里使用await异步执行
          // 使用nextTick当table表格渲染完成后执行导出表格操作
          this.$nextTick(() => {
            var xlsxParam = { raw: true } //转换成excel时，使用原始的格式
            /* 从表生成工作簿对象 */
            var wb = XLSX.utils.table_to_book(
              document.querySelector('#outTable'),
              xlsxParam
            )
            /* 获取二进制字符串作为输出 */
            var wbout = XLSX.write(wb, {
              bookType: 'xlsx',
              bookSST: true,
              type: 'array'
            })
            try {
              FileSaver.saveAs(
                //Blob 对象表示一个不可变、原始数据的类文件对象。
                //Blob 表示的不一定是JavaScript原生格式的数据。
                //File 接口基于Blob，继承了 blob 的功能并将其扩展使其支持用户系统上的文件。
                //返回一个新创建的 Blob 对象，其内容由参数中给定的数组串联组成。
                new Blob([wbout], {
                  type: 'application/octet-stream'
                }),
                //设置导出文件名称
                '合作公司' + '.xlsx'
              )
            } catch (e) {
              if (typeof console !== 'undefined') console.log(e, wbout)
            }
            this.downloadLoading = false
            return wbout
          })

          this.$message({
            type: 'success',
            message: '已成功导出Excel表格！',
            offset: 100,
            center: true,
            duration: 1000
          })
        })
        .catch(() => {
          this.$message({
            type: 'info',
            message: '已取消导出Excel表格',
            offset: 100,
            center: true,
            duration: 1000
          })
        })
    },

    // 新增
    onAdd() {
      this.dialogVisible = true
      this.dialogState = 'add'
    },

    // 点击编辑时触发
    editRow(index, row) {
      // console.log(index, row);
      this.dialogVisible = true // 是否显示编辑dialog对话框
      var rowObj = Object.assign({}, row) // 深拷贝row对象
      // rowObj.collectionAccountNumber = parseFloat(rowObj.collectionAccountNumber)
      this.validateForm = rowObj
      this.dialogState = 'edit'
    },

    // 编辑dialog取消
    dialogClose() {
      this.dialogVisible = false
      this.$refs.validateForm.resetFields() // 重置表单（清空无效）
      this.getValidateForm() // 将validateForm对象中各个属性清空
    },

    // 编辑dialog 右上角关闭按钮
    handleClose(done) {
      done() // 关闭dialog对话框
      this.$refs.validateForm.resetFields() // 重置表单（清空无效）
      this.getValidateForm() // 将validateForm对象中各个属性清空
    },

    // dialog提交
    submitForm(formName) {
      this.$refs[formName].validate(async (valid) => {
        if (valid) {
          console.log(this.validateForm)
          if (this.dialogState == 'add') {
            // 新增公司信息
            console.log('新增公司信息')
            this.addTableData()
          } else if (this.dialogState == 'edit') {
            // 编辑公司信息
            console.log('编辑公司信息')
            this.editTableData()
          }
          this.dialogVisible = false
          this.$refs.validateForm.resetFields() // 重置表单
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },

    /**
     * 画echart图表
     */
    drawEcharts() {
      // document.getElementById('myChart').removeAttribute('_echarts_instance_')
      this.myChart = this.$echarts.init(document.getElementById('myChart'))

      this.option = {
        tooltip: {
          trigger: 'axis',
          axisPointer: {
            type: 'cross',
            crossStyle: {
              color: '#999'
            }
          },
          textStyle: {
            align: 'left' // 文字居左
          }
        },
        toolbox: {
          feature: {
            dataView: { show: true, readOnly: false },
            magicType: { show: true, type: ['line', 'bar'] },
            // restore: { show: true },
            saveAsImage: { show: true }
          }
        },
        legend: {
          bottom: 0,
          data: ['当日营收/元', '当日在线订单/条']
        },
        xAxis: [
          {
            type: 'category',
            data: [],
            axisPointer: {
              type: 'shadow'
            }
          }
        ],
        yAxis: [
          {
            type: 'value',
            name: '营收',
            axisLabel: {
              formatter: '{value} 元'
            },
            max: null, // 最大订单数(null/undefined 来表示“自动计算最大值”)
            splitNumber: 6, // 刻度数
            interval: null // 限制分隔间隔
          },
          {
            type: 'value',
            name: '在线订单',
            axisLabel: {
              formatter: '{value} 条'
            },
            max: null, // 最大订单数(null/undefined 来表示“自动计算最大值”)
            splitNumber: 6, // 刻度数
            interval: null // 限制分隔间隔
          }
        ],
        series: []
      }

      // 初始化series列表（解决渲染接口数据时yAxisIndex无效的问题）
      for (let i = 0; i < 999; i++) {
        if (i % 2 == 0) {
          this.option.series[i] = {
            type: 'line',
            yAxisIndex: 0
          }
        } else {
          this.option.series[i] = {
            type: 'line',
            yAxisIndex: 1
          }
        }
      }

      this.myChart.setOption(this.option, true)

      // 图表自适应
      window.addEventListener('resize', () => {
        this.myChart.resize()
      })
    },

    // currentPage 改变时会触发
    onCurrentChange(currentPage) {
      this.currentPage = currentPage
      this.initTableData()
    },

    // pageSize 改变时会触发
    onSizeChange(pageSize) {
      // console.log(pageSize);
      this.pageSize = pageSize
      this.initTableData()
    },

    // 搜索时模式改变时触发
    onChangeMode(val) {
      if (val == 0) {
        // 单选景区多选时间范围
      } else if (val == 1) {
        // 单选日期多选景区
        this.updateSelectSpotIdList = false
        this.$nextTick(() => {
          this.updateSelectSpotIdList = true
        })

        this.updateSelectDate = false
        this.$nextTick(() => {
          this.updateSelectDate = true
        })
      } else if (val == 2) {
        // 单选景区多选日期
        this.$nextTick(() => {
          // 显示日期选项卡
          this.$refs.mydates.showPicker()
          // 默认选择今天
          this.$nextTick(() => {
            var todayEl = document.querySelector('.available.today')
            todayEl.click()

            // 隐藏日期选项卡
            this.$refs.mydates.hidePicker()
          })
        })

        this.updateSelectSpotId = false
        this.$nextTick(() => {
          this.updateSelectSpotId = true
        })
      }
    },

    // 单选景区多选日期点击时间选择器时触发
    onFocusDate() {
      this.$refs.mydates.handleClose = function handleClose() {
        if (!this.pickerVisible) return
        this.pickerVisible = false
      }
    }
  },
  components: {
    // DownloadExcel: JsonExcel,
  }
}
</script>

